import EventDispatcher = Laya.EventDispatcher;
/**事件管理类 */
export default class EventMgr {
    /**实例化 */
    private static eventDispatcher: EventDispatcher = new EventDispatcher();



    /**
     * 检查 EventDispatcher 对象是否为特定事件类型注册了任何侦听器。
     * @param	type 事件的类型。
     * @return 如果指定类型的侦听器已注册，则值为 true；否则，值为 false。
     */
    static hasListener(type: string): boolean {
        if (type != null) {
            return this.eventDispatcher.hasListener(type);
        }
        return false;
    }

    /**
   * 派发事件。
   * @param type	事件类型。
   * @param data	（可选）回调数据。<b>注意：</b>如果是需要传递多个参数 p1,p2,p3,...可以使用数组结构如：[p1,p2,p3,...] ；如果需要回调单个参数 p ，且 p 是一个数组，则需要使用结构如：[p]，其他的单个参数 p ，可以直接传入参数 p。
   * @return 此事件类型是否有侦听者，如果有侦听者则值为 true，否则值为 false。
   */
    static event(type: string, data?: any): boolean {
        if (type != null) {
            return this.eventDispatcher.event(type, data);
        }
        return false;
    }


    /**
     * 使用 EventDispatcher 对象注册指定类型的事件侦听器对象，以使侦听器能够接收事件通知。
     * @param type		事件的类型。
     * @param caller	事件侦听函数的执行域。
     * @param listener	事件侦听函数。
     * @param args		（可选）事件侦听函数的回调参数。
     * @return 此 EventDispatcher 对象。
     */
    static on(type: string, caller: any, listener: Function, args?: any[]): EventDispatcher {
        if (type != null && caller != null && listener != null) {
            return this.eventDispatcher.on(type, caller, listener, args);
        }
        return null;
    }

    /**
     * 使用 EventDispatcher 对象注册指定类型的事件侦听器对象，以使侦听器能够接收事件通知，此侦听事件响应一次后自动移除。
     * @param type		事件的类型。
     * @param caller	事件侦听函数的执行域。
     * @param listener	事件侦听函数。
     * @param args		（可选）事件侦听函数的回调参数。
     * @return 此 EventDispatcher 对象。
     */
    static once(type: string, caller: any, listener: Function, args?: any[]): EventDispatcher {
        if (type != null && caller != null && listener != null) {
            return this.eventDispatcher.once(type, caller, listener, args);
        }
        return null;
    }

    /**
     * 从 EventDispatcher 对象中删除侦听器。
     * @param type		事件的类型。
     * @param caller	事件侦听函数的执行域。
     * @param listener	事件侦听函数。
     * @return 此 EventDispatcher 对象。
     */
    static off(type: string, caller: any, listener?: Function, args?: any[]): EventDispatcher {
        if (type != null && caller != null && listener != null) {
            return this.eventDispatcher.off(type, caller, listener, args);
        }
        return null;
    }
    /**
     * 从 EventDispatcher 对象中删除指定事件类型的所有侦听器。
     * @param type	（可选）事件类型，如果值为 null，则移除本对象所有类型的侦听器。
     * @return 此 EventDispatcher 对象。
     */
    static offAll(type?: string): EventDispatcher {
        if (type != null) {
            return this.eventDispatcher.offAll(type);
        }
        return null;
    }

    /**
         * 移除caller为target的所有事件监听
         * @param	caller caller对象
         */
    static offAllCaller(caller: any): EventDispatcher {
        if (caller != null) {
            return this.eventDispatcher.offAllCaller(caller);
        }
        return null;
    }

}